@font-face {
  font-family: "EdmondSans";
  src: url("./assets/font/edmondsans/regular.woff2") format("woff2"),
    url("./assets/font/edmondsans/regular.woff") format("woff");
  font-weight: normal;
}

@font-face {
  font-family: "EdmondSans";
  src: url("./assets/font/edmondsans/medium.woff2") format("woff2"),
    url("./assets/font/edmondsans/medium.woff") format("woff");
  font-weight: bold;
}

@font-face {
  font-family: "EdmondSans";
  src: url("./assets/font/edmondsans/bold.woff2") format("woff2"),
    url("./assets/font/edmondsans/bold.woff") format("woff");
  font-weight: bolder;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: "EdmondSans";
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

header,
footer {
  flex: none;
}

main {
  flex: auto;

  display: flex;
  flex-direction: column;
  padding-top: 40px;
  padding-bottom: 40px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

pre code {
  color: rgb(136, 0, 0);
}

/* TODO: fix UI hover glitch */
.table-hover tbody tr:hover .select-network {
  background-color: #f5f9fb;
}

.selected-card {
  border-color: #510fa8;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.125) !important;
}
